.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.contenedor1 {
  background-image: url("./img/breadcrumb.jpg");
  padding: 1%;
}

.fondo {
  background-color: #f1f1f1;
  margin-bottom: 1%;
}

.button {
  background-color: #4caf50;
  /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}

.imageTable {
  width: 15%;
  height: 15%;
}
.shopping-bag {
  color: black;
}

.buttonDiscount {
  background-color: #4caf50;
  margin-left: 5%;
  border: 1px solid black;
}

.inputDiscount {
  background-color: white;
  justify-content: center;
  align-items: center;
}

.btn-add {
  background-color: #4caf50;
  border-width: 0ch;
  width: 5%;
  margin: 2%;
  border-radius: 5px;
}

.btn-substract {
  background-color: lightcoral;
  border-width: 0ch;
  width: 5%;
  margin: 2%;
  border-radius: 5px;
}

.btn-trash {
  background-color: grey;
  border-width: 0ch;
  width: 5%;
  margin: 2%;
  border-radius: 5px;
}
